	import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
	import prompt from '../../prompt.vue';
	export default {
		data() {
			return {
                index: "",
				selectValue:"",   
				imageList:[],
				date: "",
				list:[],
				pic:[],
				supplyList2:[],
				supplyList:[],
				Id:"",
				tradeId:"",
				productList:[],
				friendList:[],
				dataList:[],
				tube:'',
				com_mark:999,
				job:'',
				contract_name:'',
				edit_id:''
				
			}
		},
		onLoad:function(option){
			var _self = this
			console.log(option)
			_self.Id = option.componyId
			_self.tube = uni.getStorageSync('tube')
			_self.com_mark = uni.getStorageSync('mark')
			console.log(_self.tube)
			console.log(_self.com_mark)
			_self.getInfo()	
		},
		onShow:function(){
			var _self = this
			_self.getInfo()	
		},
		methods: {
				getInfo:function(){
					var _self =this
					 uni.request({
					 	url:_self.$api+"dockingManager/tradeQuery", 
					 	data:{id:_self.Id}, 
						method:"GET",
						success:function(res){
							console.log(res)
							_self.list = res.data[0]
							_self.tradeId = res.data[0].id
							uni.setStorageSync("tradeId",res.data[0].id)
							// _self.getNeedInfo()
							// _self.getNeedInfo1()
							// _self.getProduct() 
							_self.getMyFriend()  
							_self.getDataList()
							
						}
					 })
				}, 
				/**
				 * 修改企业
				 */
				goEdit:function(){
					var _self =this
					 uni.navigateTo({
					 	url:'../editMyCompony/editMyCompony?id='+_self.Id
					 })
				},
				/**
				 * 需求更多
				 */
				gotoSupplyMore:function(){
					var _self = this
					uni.navigateTo({
						url:"../myComponyNeed/myComponyNeed?id="+_self.tradeId
					})
				},
				/**
				 * 供应更多 
				 */
				gotoSupplyMore1:function(){
					var _self = this
					uni.navigateTo({
						url:"../myComponyApply/myComponyApply?id="+_self.tradeId
					})
				},
				/**
				 * 产品服务
				 */
				getProduct:function(){
					var _self = this
					uni.request({
						url:_self.$api+"dockingManager/totalNewQuery",
						data:{pull:18,id:0,optionId:uni.getStorageSync("openId"),branch:0,trade:_self.tradeId},
						method:"GET",
						success:function(res){
							_self.productList = res.data.slice(0,2) 
							console.log(res)
						}
					})
				},
				gotoSupplyMore2:function(mark,branch,part_name){
					var _self = this
					uni.navigateTo({
					url:"../../pageChildren/componyProduct/componyProduct?mark="+mark+'&branch='+branch+'&part_name='+part_name
						//url:"../myComponyProduct/myComponyProduct?mark="+mark+'&branch='+branch+'&part_name='+part_name
					})
				},
				goInfoDetail:function(id,mark){
					var _self = this
					uni.navigateTo({
						url:"../myComponyNeedDetail/myComponyNeedDetail?id="+id+"&mark="+mark+"&tradeId="+_self.tradeId
					})
				},
				goProductDetail:function(id,branch,mark,part_name){
					var _self = this
					uni.navigateTo({
						url:"../../pageChildren/productDetail/productDetail?id="+id+"&branch="+branch+"&part_name="+part_name+'&mark='+mark+'&Id='+_self.tradeId
					})
				},
				/**
				 * 模块查询
				 */
				getDataList:function(){
					var _self = this
					uni.request({
						url:_self.$api+"dockingManager/titleBranchQuery",
						data:{optionId:uni.getStorageSync("openId"),id:uni.getStorageSync('tradeId')},
						method:"GET",
						success:function(res){
							_self.dataList = res.data
							console.log(res)
						}
					})
					
				},
				/**
				 * 我的同事
				 */
				getMyFriend:function(){
					var _self = this
					uni.request({
						url:_self.$api+"dockingManager/tradeWorkQuery",
						data:{optionId:uni.getStorageSync("openId"),branch:1,live:4},
						method:"GET",
						success:function(res){
							_self.friendList = res.data
							console.log(res)
						}
					})
				},
				gotoManager:function(){
					var _self = this
					uni.navigateTo({
						url:"../myFriends/myFriends"
					})
				},
				/**
				 * 操作
				 */
			prompt:function(){
				this.$refs.prompt.show();
			},
			onConfirm:function(e){
				var _self = this
				console.log(e);
				let _cost = e;
				if (_cost == '') {
				 uni.showToast({
				 	'title':'请输入服务模块名称',
					'icon':'none'
				 })
				 console.log('你还未输入');
				 return;
				}
				else{  
				  uni.showModal({
				  	title: '提示',
				  	content: '是否新增服务模块:'+_cost, 
				  	showCancel: false,
				  	confirmText: "确定",
					success:function(){
						uni.request({
							url:_self.$api+'dockingManager/titleBranchAdd',
							data:{id:uni.getStorageSync('tradeId'),name:_cost},
							success:function(res){
								if(res.data == 1){
									 uni.showToast({
										'title':'新增成功',
									})
									_self.$refs.prompt.hide();
									_self.getInfo()
									return
								}else{
									 uni.showToast({
										'title':'新增失败，请联系工作人员',
									})
									_self.$refs.prompt.hide();
									return false
								}
							}
						})
					}
				  })
				}
			},
			onCancel:function(){
				this.$refs.prompt.hide();
				this.$refs.prompt.cost = '';
			},
			/**
			 * @param {Object} id
			 * 修改分类
			 */
			editprompt:function(id){
				var _self = this
				console.log(id)
				_self.edit_id = id
				console.log(_self.$refs.editprompt)
				_self.$refs.editprompt[0].show();
			},
			onConfirm1:function(e){
				
				var _self = this
				console.log(_self.edit_id)
				console.log(_self.Id)
				console.log(e);
				let _cost = e;
				if (_cost == '') {
				 uni.showToast({
				 	'title':'请输入服务模块名称',
					'icon':'none'
				 })
				 console.log('你还未输入');
				 return;
				}
				else{  
				  uni.showModal({
				  	title: '提示',
				  	content: '是否修改服务模块:'+_cost, 
				  	showCancel: false,
				  	confirmText: "确定",
					success:function(){
						uni.request({
							url:_self.$api+'dockingManager/titleBranchUpdate',
							data:{id:_self.edit_id,name:_cost,mark:_self.tradeId},
							success:function(res){
								console.log(res)
								if(res.data == 1){
									 uni.showToast({
										'title':'修改成功',
									})
									_self.$refs.editprompt[0].hide();
									_self.getInfo()
									return
								}else{
									 uni.showToast({
										'title':'修改失败，请联系工作人员',
									})
									_self.$refs.editprompt[0].hide();
									return false
								}
							}
						})
					}
				  })
				}
			},
			onCancel1:function(){
				this.$refs.editprompt[0].hide();
				this.$refs.editprompt[0].cost = '';
			},
			/**
			* 跳转发布内容
			*/
		   goServerContent:function(id){
			   uni.navigateTo({
			   	url:'../serverContent/serverContent?id='+id
			   })
		   },
		   /**
			* 删除分类
			*/
		   deleteContent:function(id){
			   var _self = this
			   uni.showModal({
				title: '提示',
				content: '是否删除此模块',
				success: function (res) {
						if (res.confirm) {
							uni.request({
								url:_self.$api+'dockingManager/titleBranchDelete',
								data:{id:id},
											success:function(res){
												if(res.data==1){
													uni.showToast({
														title:'删除成功'
													})
													_self.getInfo()
													return
												}else{
													uni.showToast({
														title:'删除失败',
														icon:'none'
													})
													return
												}
											}
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});  
		   }
			

		},
		 components: {uniCollapse,uniCollapseItem,prompt},
		  computed: {
		   
		 },
		 filters:{
			 getPic:function(res){
			 	return res.split(',')[0]
			 },
			 getTime:function(res){
			 	return res.slice(0,11)
			 },	
			 changeData:function(data){
			 		 if(data == undefined || data==null || data ==''){
			 			 return '暂无'
			 		 }else{
			 			 return data
			 		 }
			 	},
		 }
	}
